body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


header {
    background-color: rgb(0, 0, 0);
    color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

  }
  
  .navbar-logo {
    font-weight: bold;
    color: white;
    text-decoration: none;
    
  }
  .navbar-logo >img {
    position: absolute;
    left: 2vw;
    top: 0.3vw;
    z-index: 99999;
    width: 5vw;
    height: 5vw;
    border-radius: 100%;
  }
  
  .navbar-menu {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  


  
  .navbar-menu a {
    color: #fff;
    text-decoration: none;
    padding: 10px 15px;
    border-radius: 5px;
    transition: all .3s linear;
    font-family: 'Baloo Thambi 2', cursive;
  }
  .navbar-menu a:hover{
    color: #ca9720;
    border-bottom: 2px solid #ca9720;

  }

  
  .navbar-toggle {
    display: none;
    cursor: pointer;
  }
  
  .navbar-toggle span {
    display: block;
    height: 2px;
    width: 25px;
    background-color: #fff;
    margin-bottom: 5px;
  }
  
  @media screen and (max-width: 768px) {
    .navbar-menu {
      transition: all .5s linear;
      display: none;
      flex-direction: column;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: black;
      padding: 20px;
    }
  
    .navbar-menu li {
      margin: 10px 0;
    }
  
    .navbar-toggle {
      display: block;
    }
    
    .navbar-menu.show {
      transition: all .5s linear;
      display: flex;
     justify-content: center;
      align-items: center;
      grid-gap: 7vw;
      gap: 7vw;
    }
  }
  
